import config from "../config";
import authHelper from "../utils/authHelper";

type PersonLocation = "close" | "home" | "away";
type UserAlarmMode = "all" | "some" | "none";
type AlarmMode = UserAlarmMode | "exit_delay" | null;

export interface DevicesType {
  isSunset: boolean;
  isDark: boolean;

  motionKitchen: boolean;
  motionEntry: boolean;
  motionDining: boolean;
  motionHall: boolean;
  motionBathroom: boolean;
  motionLiving: boolean;
  // motionGarage: boolean;

  cameraMotionGarage: boolean;
  cameraMotionDoor: boolean;

  lightsDiningRoom: boolean;
  lightsBathroom: boolean;
  lightsEntry: boolean;
  lightsHall: boolean;
  lightsBreakfastArea: boolean;
  lightsLivingRoom: boolean;

  speakerReceiverPower: boolean;
  lightsBackyard1: boolean;
  lightsBackyard2: boolean;
  siren: boolean;

  // tvPower: boolean;

  doorBack: boolean;
  doorCar: boolean;
  doorFront: boolean;
  doorGarageHome: boolean;
  doorKitchen: boolean;
  doorSide: boolean;
  // doorInterior: boolean;
  // doorGate: boolean;

  artemAtHome: PersonLocation;
  yueAtHome: PersonLocation;
  xiaohongAtHome: PersonLocation;

  alarm: AlarmMode;

  // thermostat: number;
}

class DeviceProvider {
  async getAll(): Promise<DevicesType> {
    const token = authHelper.getToken();
    if (token === null) throw new Error("UNAUTHORIZED");
    const res = await fetch(`${config("serverPath")}/api/all_devices`, {
      headers: {
        Authorization: token
      }
    });
    const msg = await res.json();
    return msg;
  }

  async getHistory(device: string): Promise<{ value: number; time: string }[]> {
    const token = authHelper.getToken();
    if (token === null) throw new Error("UNAUTHORIZED");
    const res = await fetch(
      `${config("serverPath")}/api/all_devices/${device}/history`,
      {
        headers: {
          Authorization: token
        }
      }
    );
    const msg = await res.json();
    return msg;
  }

  async getLocations(): Promise<any> {
    const token = authHelper.getToken();
    if (token === null) throw new Error("UNAUTHORIZED");
    const res = await fetch(
      `${config("serverPath")}/api/all_devices/locations`,
      {
        headers: {
          Authorization: token
        }
      }
    );
    const msg = await res.json();
    return msg;
  }
  async update(): Promise<any> {
    const token = authHelper.getToken();
    if (token === null) throw new Error("UNAUTHORIZED");
    const res = await fetch(`${config("serverPath")}/api/all_devices/update`, {
      method: "POST",
      headers: {
        Authorization: token
      }
    });
    const msg = await res.json();
    return msg;
  }

  async put(
    id: keyof DevicesType,
    status: boolean
  ): Promise<{ status: boolean }> {
    const token = authHelper.getToken();
    if (token === null) throw new Error("UNAUTHORIZED");
    const res = await fetch(
      `${config("serverPath")}/api/all_devices/${id}/${status}`,
      {
        method: "PUT",
        headers: {
          Authorization: token,
          "Content-Type": "application/json"
        }
      }
    );
    const msg = await res.json();
    return msg;
  }
}

const deviceProvider = new DeviceProvider();

export default deviceProvider;
export { DeviceProvider };
