import * as d3 from "d3";
import React, { useState } from "react";
import { Selection } from "d3";
import { uuidv4 } from "../../utils/uuid";

interface DataType {
  value: number;
  date: Date;
  name?: string;
}

export interface LineType {
  data: DataType[];
  name?: string;
}

interface PropTypes {
  width: number;
  height: number;
  lines: LineType[];
  getCallout: { (point: DataType): string };
}

type GenericSelection = Selection<any, any, any, any>;

// const colors = ["steelblue"];
const colors = [
  "#1f77b4",
  "#ff7f0e",
  "#2ca02c",
  "#d62728",
  "#9467bd",
  "#8c564b",
  "#e377c2",
  "#7f7f7f",
  "#bcbd22",
  "#17becf"
];

const LineChart: React.FC<PropTypes> = props => {
  const drawChart = () => {
    const lines = props.lines;
    const height = props.height;
    const width = props.width;

    const margin = { top: 20, right: 30, bottom: 60, left: 40 };

    let allData = lines.flatMap(line => line.data);

    const xDomain = d3.extent(allData, d => d.date) as [Date, Date];
    const yDomain = d3.extent(allData, d => d.value) as [number, number];

    const xRange = [margin.left, width - margin.right];
    const yRange = [height - margin.bottom, margin.top];

    const x = d3
      .scaleTime()
      .domain(xDomain)
      .range(xRange);
    const y = d3
      .scaleLinear()
      .domain(yDomain)
      .nice()
      .range(yRange);

    const xAxis = (g: GenericSelection) =>
      g.attr("transform", `translate(0,${height - margin.bottom})`).call(
        d3
          .axisBottom(x)
          .ticks(width / 80)
          .tickSizeOuter(0)
      );

    const yAxis = (g: GenericSelection) =>
      g.attr("transform", `translate(${margin.left},0)`).call(d3.axisLeft(y));

    const d3Bisect = d3.bisector((d: DataType) => d.date).left;
    const bisect = (mouse: [number, number]) => {
      // mouse = [x,y] coordinates of the mouse

      let closestPoints = lines.map(line => {
        const data = line.data;
        const date = x.invert(mouse[0]);
        const index = d3Bisect(data, date, 1);

        // the index found acts as a ceil(position) so we want to see if prior element is closer
        const a = data[index - 1];
        const b = data[index];

        if (!a) return b;
        if (!b) return a;

        const dateDiff = (d1: Date, d2: Date) => d1.getTime() - d2.getTime();

        return dateDiff(date, a.date) > dateDiff(b.date, date) ? b : a;
      });

      const my = y.invert(mouse[1]);

      let closest = closestPoints[0];
      closestPoints.forEach(point => {
        if (Math.abs(point.value - my) < Math.abs(closest.value - my))
          closest = point;
      });

      return closest;
    };

    const callout = (g: GenericSelection, value: string) => {
      if (!value) return g.style("display", "none");

      g.style("display", null)
        .style("pointer-events", "none")
        .style("font", "10px sans-serif");

      const path = g
        .selectAll("path")
        .data([null])
        .join("path")
        .attr("fill", "white")
        .attr("stroke", "black");

      const text = g
        .selectAll("text")
        .data([null])
        .join("text")
        .call(text =>
          text
            .selectAll("tspan")
            .data((value + "").split(/\n/))
            .join("tspan")
            .attr("x", 0)
            .attr("y", (d, i) => `${i * 1.1}em`)
            .style("font-weight", (_, i) => (i ? null : "bold"))
            .text(d => d)
        );

      const { y, width: w, height: h } = (text as any).node().getBBox();

      text.attr("transform", `translate(${-w / 2},${15 - y})`);
      path.attr(
        "d",
        `M${-w / 2 - 10},5H-5l5,-5l5,5H${w / 2 + 10}v${h + 20}h-${w + 20}z`
      );
    };

    const lineDef = d3
      .line<DataType>()
      .defined(d => !isNaN(d.value))
      .x(d => x(d.date))
      .y(d => y(d.value));

    const svg = d3
      .select(`#${chartId}`)
      .append("svg")
      .attr("viewBox", [0, 0, width, height] as any);

    svg.append("g").call(xAxis);
    svg.append("g").call(yAxis);

    lines.forEach((line, idx) => {
      svg
        .append("path")
        .datum(line.data)
        .attr("fill", "none")
        .attr("stroke", colors[idx % colors.length])
        .attr("stroke-width", 1.5)
        .attr("stroke-linejoin", "round")
        .attr("stroke-linecap", "round")
        .attr("d", lineDef);
    });

    const tooltip = svg.append("g");

    svg.on("touchmove mousemove", function() {
      const point = bisect(d3.mouse(this));

      tooltip
        .attr("transform", `translate(${x(point.date)},${y(point.value)})`)
        .call(callout, props.getCallout(point));
    });

    svg.on("touchend mouseleave", () => tooltip.call(callout, null));
  };

  const [chartId] = useState("chart-" + uuidv4());
  const [needsRender, needsRenderSet] = useState(true);
  if (needsRender) {
    needsRenderSet(false);
    setTimeout(drawChart);
  }

  return <div id={chartId} />;
};

export { LineChart };
