import config from "../config";
import authHelper from "../utils/authHelper";

export type AlarmMode = "all" | "some" | "none" | "exit_delay" | null;

class AlarmProvider {
  async setAlarmMode(mode: AlarmMode): Promise<AlarmMode> {
    const token = authHelper.getToken();
    if (token === null) throw new Error("UNAUTHORIZED");
    const res = await fetch(`${config("serverPath")}/api/alarm/${mode}`, {
      method: "POST",
      headers: {
        Authorization: token
      }
    });
    const msg = await res.text();
    return JSON.parse(msg).mode;
  }
  async getAlarmMode(): Promise<AlarmMode> {
    const token = authHelper.getToken();
    if (token === null) throw new Error("UNAUTHORIZED");
    const res = await fetch(`${config("serverPath")}/api/alarm`, {
      method: "GET",
      headers: {
        Authorization: token
      }
    });
    const msg = await res.text();
    return JSON.parse(msg).mode;
  }
}

const alarmProvider = new AlarmProvider();

export default alarmProvider;
export { AlarmProvider };
