function fetchTemperature() {
  return Promise.all([
    deviceProvider
      .getHistory("climateTemperature")
      .then(res => res.map(r => ({ name: "Indoor Temperature", ...r }))),
    deviceProvider
      .getHistory("weatherTemperature")
      .then(res => res.map(r => ({ name: "Outdoor Temperature", ...r }))),
    deviceProvider
      .getHistory("climateTargetTemperature")
      .then(res => res.map(r => ({ name: "Thermostat Temperature", ...r })))
  ]).then(res =>
    res.map(r => ({
      name: r[0].name,
      data: r.map(r => ({
        name: r.name,
        date: new Date(r.time),
        value: ctof(r.value / 1000000)
      }))
    }))
  );
}

