function generateForecast(res: Forecast): LineType[] {
  const hours: { [hour: string]: number[] } = {};

  function getDayByHour(hour: HourData) {
    let day: DayData | null = null;
    for (let i = 0; i < res.forecast.daily.length; i++) {
      day = res.forecast.daily[i];
      const nextDay = res.forecast.daily[i + 1];
      if (!nextDay || hour.time < nextDay.date) return day;
    }
    return day;
  }

  // get an approximation of how warm each hour is relative to the day's low and high
  res.forecast.hourly.forEach(hourData => {
    const dayData = getDayByHour(hourData);

    const date = new Date(hourData.time * 1000);
    const hour = date.getHours();

    if (!dayData) return;

    const dayRange = dayData.high_temperature - dayData.low_temperature;
    const ratio = (hourData.temp - dayData.low_temperature) / dayRange;

    hours[hour] = hours[hour] || [];
    hours[hour].push(ratio);
  });

  // average those approxmiations
  const hourAvg = Object.keys(hours).map(
    hour => hours[hour].reduce((a, b) => a + b) / hours[hour].length
  );

  // build out the line using real forecast data
  let hour: number;
  let firstHour: number;
  const data: LineType["data"] = res.forecast.hourly.map(hourData => {
    hour = hourData.time * 1000;
    if (!firstHour) firstHour = hour;
    return {
      date: new Date(hour),
      value: hourData.temp,
      name: "Forecast"
    };
  });

  // append forecast of forecast
  res.forecast.daily.forEach(dayData => {
    const dayStart = dayData.date * 1000;
    for (let h = 0; h < 24; h++) {
      const t = dayStart + h * 60 * 60 * 1000;
      if (t >= firstHour && t <= hour) continue;

      const dayRange = dayData.high_temperature - dayData.low_temperature;
      const value = hourAvg[h] * dayRange + dayData.low_temperature;

      data.push({
        date: new Date(t),
        value,
        name: "Forecast"
      });
    }
  });

  data.sort((a, b) => (a.date < b.date ? -1 : 1));

  return [
    {
      name: "Forecast",
      data
    }
  ];
}

function fetchWindSpeed() {
  return deviceProvider.getHistory("weatherWindSpeed").then(res => [
    {
      name: "Wind Speed",
      data: res.map(r => ({
        name: "Wind Speed",
        date: new Date(r.time),
        value: r.value / 1000000
      }))
    }
  ]);
}

function aggregateLines(data: LineType["data"][]) {
  let min = 0;
  let max = 0;

  data.forEach(unit =>
    unit.forEach(point => {
      if (min === 0 || point.date.getTime() < min) min = point.date.getTime();
      if (min === 0 || point.date.getTime() > max) max = point.date.getTime();
    })
  );

  let pointers = data.map(_ => 0);
  let lastStatus = data.map(_ => 0);

  const result = [] as { date: Date; value: number }[];
  for (let t = min; t <= max; t += 15 * 60 * 1000) {
    let currStatus = [...lastStatus];
    for (let j = 0; j < pointers.length; j++) {
      while (true) {
        if (pointers[j] >= data[j].length) break;
        const p = data[j][pointers[j]];
        if (p.date.getTime() > t) break;
        currStatus[j] = lastStatus[j] || p.value;
        lastStatus[j] = p.value;
        pointers[j]++;
      }
    }
    const unitCount = currStatus.reduce((a, b) => a + b);
    result.push({
      date: new Date(t),
      value: unitCount
    });
  }
  return result;
}

async function fetchAggregate(d: DevicesType, regex: RegExp, name: string) {
  const devices = await Promise.all(
    Object.keys(d)
      .filter(d => d.match(regex))
      .map(d => deviceProvider.getHistory(d))
  );

  let mappedDevices = devices.map(device =>
    device.map(point => ({
      date: new Date(point.time),
      value: point.value >= 1 ? 1 : 0
    }))
  );
  const data = aggregateLines(mappedDevices);

  return [{ name, data }];
}

