import React, { useState } from "react";
import styles from "./alarmSettings.module.css";

import DisarmedIcon from "./disarmedIcon";
import HomeIcon from "./homeIcon";
import AwayIcon from "./awayIcon";
import alarmProvider, { AlarmMode } from "../../providers/alarmProvider";
import { wait } from "../../utils/asyncHelper";

interface PropType {
  mode?: AlarmMode;
}

const AlarmSettings: React.FC<PropType> = props => {
  const [mode, setMode] = useState(props.mode);

  if (!mode) {
    alarmProvider
      .getAlarmMode()
      .then(setMode)
      .catch(e => console.error(e));
  }

  const changeMode = (mode: AlarmMode) => async () => {
    setMode(mode);
    mode = await alarmProvider.setAlarmMode(mode);
    setMode(mode);
    await wait(45 * 1000);
    mode = await alarmProvider.getAlarmMode();
    setMode(mode);
  };

  return (
    <div className={styles.container}>
      <div
        onClick={changeMode("none")}
        className={`${styles.icon} ${
          mode === "none" ? `${styles.disarmed} ${styles.selected}` : ""
        }`}
      >
        <DisarmedIcon />
      </div>
      <div
        onClick={changeMode("some")}
        className={`${styles.icon} ${
          mode === "some" ? `${styles.home} ${styles.selected}` : ""
        }`}
      >
        <HomeIcon />
      </div>
      <div
        onClick={changeMode("all")}
        className={`${styles.icon} ${
          mode === "all" || mode === "exit_delay"
            ? `${styles.away} ${styles.selected}`
            : ""
        }`}
      >
        <AwayIcon />
      </div>
      <div className={styles.subtext}>
        {mode === "none"
          ? "Disarmed"
          : mode === "some"
          ? "Home"
          : mode === "all"
          ? "Away"
          : mode === "exit_delay"
          ? "Exit Delay"
          : null}
      </div>
    </div>
  );
};

export default AlarmSettings;
